<template>
  <div id="map"></div>
</template>
<script>
import mapboxgl from 'mapbox-gl'
export default {
  name: 'mapboxgl',
  data () {
    return {
      map: {}
    }
  },
  mounted () {
    mapboxgl.accessToken = 'pk.eyJ1IjoicXVvcmF1ayIsImEiOiJjamUxcHNxdzExbmphMnhwOW8zMHV5ZmQxIn0.zJdXQSrVdgmYsg9xitYEzw'
    var map = new mapboxgl.Map({
      container: 'map',
      style: 'mapbox://styles/mapbox/streets-v9',
      center: [-2, 55],
      zoom: 5
    })
    map.on('load', () => {
      map.addSource('communities', {'type': 'geojson', data: `${process.env.api}/layer`})
      map.addLayer({
        'id': 'communities',
        'type': 'symbol',
        'source': 'communities',
        'layout': {
          'text-field': '{name}',
          'text-font': ['Open Sans Semibold', 'Arial Unicode MS Bold'],
          'text-offset': [0, 0.6],
          'text-anchor': 'top'
        }
      })
    })
    this.map = map
  }
}
</script>
<style>
#map {
}
<style>



